*{
  margin: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
@import url('https://fonts.googleapis.com/css2?family=Cousine&family=Montserrat:wght@100;700;800&family=Roboto:ital,wght@0,300;0,400;0,500;1,100;1,300;1,400&display=swap');
:root{
  /* Variaveis */
  --branco:#FFFFFF;
  --preto:#000000;
  --azul:#38BEEF;
  --amarelo:#F2C81C;
  --verde:#111F25;
  --verde-preto:#0A1316;
  --verde-wpp:#25D366;
  --cinza-claro:#B2B2B2;
  --cizan-dark:#757575;
  --logo:url('../imgs/logo.svg');
  --avatar:url('../imgs/avatar.png'); 
  --bgTop:url('../imgs/bgTop.webp');
  --wpp:url('../imgs/wpp.svg');
  --btwpp:url('../imgs/btwpp.svg');
  --iconGraphic:url('../imgs/iconeDesigner.svg');
  --iconFrontEnd:url('../imgs/iconFrontEnd.svg');
  --iconTi:url('../imgs/iconTi.svg');
  --bgBottom:url('../imgs/bgBottom.svg');
  --clienteBankai:url('../imgs/bankai.jpg');
  --avatarComent:url('../imgs/avatarComent.jpg');
  --iconLinkedin:url('../imgs/iconLinkedin.svg');
  --iconInsta:url('../imgs/iconInsta.svg');
  --iconCv:url('../imgs/iconCv.svg');
}
html{
  scroll-behavior: smooth;
}
body::-webkit-scrollbar {
  width: 8px;               /* width of the entire scrollbar */
}
body::-webkit-scrollbar-track {
  background: var(--preto);        /* color of the tracking area */
}
body::-webkit-scrollbar-thumb {
  background-color: var(--azul);    /* color of the scroll thumb */     /* roundness of the scroll thumb */  /* creates padding around scroll thumb */
}
body{
    background: var(--bgBottom) no-repeat var(--preto);
    background-position:center bottom;
}
#top{
  background:var(--bgTop) no-repeat;
  background-size: cover;
  background-position:center top;
  background-attachment: fixed;
  width: 100%;
  height: 90vh;
}
#menu.rolagem{
  width: 100%;
  background-color: var(--preto);
  padding:1vh 0;
}
#menu{
  /* ESTILO MENU */
  display: flex;
  position: fixed;
  top:0;
  left: 0;
  width:100%;
  margin:0 auto;
  align-items: flex-end;
  padding-top: 50px;
  transition: all 0.5s ease-in-out;
  z-index: 9;
}
#menu-mobile{
  display: none;
  flex-direction: column;
  justify-content: space-between;
  width: 32px;
  height: auto;
}
.hamburguer{
  content: "";
  border-top: 4px #fff solid;
  width: 100%;
}
.hamburguer::before,.hamburguer::after{
  content: "";
  display: block;
  width: 100%;
  height: 4px;
  margin-top: 5px;
  background-color: var(--branco);
}
#menu .menu{
  /* ESTILO MENU */
  display: flex;
  width: 65%;
  margin: 0 auto;
  align-items: center;
  justify-content: space-between;
  padding:10px 0;
}
.logo{
  /* STYLE LOGO MENU */
  width: 112px;
  height: 36px;
  background:var(--logo) no-repeat center;
  background-size: cover;
  transition: all 0.5s ease-in-out;
}
.menu nav{
   /* MENU NAV */
  display: flex;
  text-align: center;
  align-items: center;
}
.menu nav ul{
  /* MENU NAV */
  display: flex;
  list-style: none;
  gap: 40px;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.menu nav ul li a{
  /* MENU NAV */
  text-decoration: none;
  color: var(--azul);
  letter-spacing: 1px;
  font-size: 16px;
  font-family:'Roboto', sans-serif;
  padding: 8px;
  font-weight: 500;
  border-radius: 2px;
  transition: all 1s;
}
.menu nav ul li a:hover{
  border-radius: 5px;
  color: var(--branco);
  background-color: var(--azul);
}
/* Configuraçoes de Box Portfolio */ 
.portfolio{
  display: grid;
  align-items: flex-end;
  width: 60%;
  padding-top:13%;
}
#portfolio{
  display: flex;
  width: 55%;
  height: 80%;
  margin:0 auto;
  padding-top: 10%;
}
#portfolio .nome h1{
  color:var(--branco);
  font-family: 'Montserrat', sans-serif;
  font-weight: bold;
  letter-spacing: 1px;
  font-size: 7.1vb;
}
#portfolio .nome span{
  display: grid;
  color: var(--branco);
  font-family: 'Courier New', Courier, monospace;
  font-size: 1.6em;
}
.btwpp{
  width: 280px;
  height: 60px;
  background:var(--btwpp) no-repeat center center;
  background-size: contain;
  margin-bottom: 10%;
  transition:all 0.5s ease-in-out;
}
.avatar{
  background:var(--avatar) no-repeat center center;
  background-size: 100%;
  display: flex;
  width: 50%;
  height: 100%;
  transition: all 0.5s linear;
}
/* Estilização Cards Designer, Front End, Informatica */
#jobs{
  display: flex;
  flex-wrap: wrap;
  margin:0 auto;
  width: 65%;
  justify-content: space-between;
  animation: scroller ease-in-out;
  animation-timeline: view();
  animation-range: cover 0% cover 50%;
  z-index: 1;
}
#jobs:target{
  scroll-margin-top:30vh;
}
#clientes:target{
  scroll-margin-top:24vh;
}
.oquefaco, .clientes, .estaodizendo, .fiqueligado{
  text-align: center;
  width: 100%;
  font-family: 'Roboto',sans-serif;
  font-size: 1.4vw;
  padding-bottom: 4%;
  color: var(--cinza-claro);
}
.oquefaco, .clientes, .estaodizendo{
  animation: scroller ease-in-out;
  animation-timeline: view();
  animation-range: cover 0 cover 50%;
}
.fiqueligado{
  animation: scroller ease-in-out;
  animation-timeline: view();
  animation-range: cover 0 cover 20%;
}
.oquefaco{
  margin-top: -5%;
}
.fiqueligado{
  margin-top: 5% !important;
}
.clientes{
  margin-top: 4% !important;
}
.estaodizendo{
  margin:2% 0 !important;
}
.card-design, .card-front-end, .card-ti{
  display:grid;
  text-align: center;
  width: 28%;
  height: 50vh;
  background-image: linear-gradient(to top, var(--verde) , var(--verde-preto));
  border-radius: 10px;
  align-items:flex-start;
  padding-bottom: 1vh;
  transition: all 1s ease-in-out;
}
.card-design:hover, .card-front-end:hover, .card-ti:hover{
  background-image: linear-gradient(to top, var(--verde-preto) , var(--verde));
  transform: scale(1.1);
}
.card-design h1, .card-front-end h1, .card-ti h1{
  font-size: 1.5vw;
  color: var(--branco);
  letter-spacing: 1px;
}
.card-design p, .card-front-end p, .card-ti p{
  font-size: 1vw;
  color: var(--branco);
  font-family: 'Courier New', Courier, monospace;
  padding:0 20px;
  letter-spacing: 1px;
}
.card-design a, .card-front-end a, .card-ti a{
   padding:4% 16%;
   background-color: var(--branco);
   text-decoration: none;
   border-radius: 6px;
   font-family: sans-serif;
   font-weight: 400;
   color: var(--verde);
}
.card-design-icon{
  display: flex;
  width: 100%;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
  background:var(--iconGraphic) no-repeat center center var(--verde);
  background-size: 20%;
  height: 10vh;
}
.card-front-end-icon{
  display: flex;
  width: 100%;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
  background:var(--iconFrontEnd) no-repeat center center var(--verde);
  background-size: 20%;
  height: 10vh;
}
.card-ti-icon{
  display: flex;
  width: 100%;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
  background:var(--iconTi) no-repeat center center var(--verde);
  background-size: 20%;
  height: 10vh;
}
/* Estilização Box Clientes */
#clientes{
  display: flex;
  width:65%;
  margin: 0 auto;
  height: auto;
  justify-content:space-between;
  flex-wrap: wrap;
  animation: scroller ease-in-out;
  animation-timeline: view();
  animation-range: cover 0% cover 50%;
}
#box-cliente{
  display: flex;
  width:47%;
  height: 230px;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 40px;
  background-color: var(--preto);
  transition: all 1s ease-in-out;
}
#box-cliente:hover{
  transform: scale(1.15);
}
.img-cliente{
  width: 100%;
  height: 100%;
  background: var(--clienteBankai) no-repeat center;
  background-size: cover;
  border-radius: 6px;
}
.desc-cliente{
  display: flex;
  flex-direction: column;
  text-align: center;
  width: 100%;
  padding: 10px;
}
.t-cliente{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.t-cliente h1, .t-cliente p{
  display: flex;
  font-size: 80%;
  color: var(--cinza-claro);
  font-family: 'Roboto', sans-serif;
  padding:5px 0;
  flex-wrap: wrap;
}
.t-cliente p{
  font-size: 0.1 !important;
}
.bt-cliente{
  width: auto;
  background-color: var(--azul);
  font-family: 'Roboto',sans-serif;
  text-align: center;
  margin:20px auto;
  padding: 10px 20px;
  border-radius: 6px;
}
.bt-cliente a{
  text-decoration: none;
  color:var(--branco);
  font-weight: normal;
  font-weight: 400;
  letter-spacing: 0.5px;
}
/* Estilização de Comentarios */
#comentarios{
  display: flex;
  width: 65%;
  justify-content: space-between;
  margin: 0 auto;
  animation: scroller ease-in-out;
  animation-timeline: view();
  animation-range: cover 0 cover 50%;
}
#comentario{
  display: flex;
  width: 46%;
  justify-content: space-between;
  transition: all 1s ease-in-out;
}
#comentario:hover{
  transform: scale(1.1);
}
.foto-coment{
  width: 70px;
  height: 70px;
  background:var(--avatarComent) no-repeat center;
  background-size: cover;
  border-radius: 100%;
}
.desc-coment{
  display: flex;
  flex-wrap: wrap;
  width: 80%;
  padding-top: 2%;
  align-items: flex-end;
}
.desc-coment h1, .desc-coment p{
  display: flex;
  font-size: 1.5em;
  color: var(--azul);
  font-family: 'Montserrat', sans-serif;
  padding: 5px 10px;
  flex-wrap: wrap;
}
.desc-coment p{
  margin-top: -10px !important;
  font-size: 0.7em !important;
  font-family: 'Roboto',sans-serif;
  color: var(--cinza-claro) !important;
}
/* Estilizações  Rodape */
#rodape{
  display: flex;
  width: 65%;
  justify-content: space-between;
  margin:1% auto;
  animation: scroller ease-in-out;
  animation-timeline: view();
  animation-range: cover 0 cover 20%;
}
#linkedin, #insta, #curriculum{
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
  padding: 20px;

}
.label{
  transition:all 0.5s;
}
.label:hover{
  color: var(--azul); 
}
.iconLinkedin, .iconInsta, .iconCurriculum{
  width: 80px;
  height: 80px;
  background: var(--iconLinkedin) no-repeat center;
}
.label{
  font-size: 0.7em;
  font-family:'Roboto',sans-serif;
  font-weight: 600;
  color: var(--branco);
  padding: 10px 0;
  text-align: center;
}
.iconInsta{
  background: var(--iconInsta) no-repeat center !important;
}
.iconCurriculum{
  background: var(--iconCv) no-repeat center !important;
}
/* Configurações de Animação */
@keyframes scroller{
  0%{
    opacity: 0;
    scale:0 0;
    color: var(--preto);
}
  20%{
      opacity: 0;
      scale:0 0;
      color: var(--preto);
  }
  100%{
      opacity: 1;
      scale: 1 1;
      color: var(--branco);
    }
}
