/* Media Queries Responsividade */
@media screen and (max-width: 796px){
    .menu nav{
      visibility: hidden;
      position: absolute;
      top: 10vh;
      left: 0;
      background-color:var(--preto);
      width: 100%;
      height: calc(100vh - 5rem);
      text-align: center;
      z-index: 9;
      padding: 5rem 0;
    }
  
    .menu nav ul{
      display: grid;
      text-align: center;
      justify-content: center;
      align-items: center;
      margin: 0;
      padding: 0;
    }
    .menu nav ul li a{
      padding:10px 10rem;
    }
    #menu-mobile{
      display: flex;
    }
    #portfolio{
      display: flex;
      width: 90%;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
    #top{
      height: 60rem !important;
    }
    .avatar{
      width: 65%;
      order: -1;
    }
   .oquefaco{
      margin-top: -20%;
    }
    .portfolio{
      display: grid;
      width: 90%;
      text-align: center;
      margin: 0 auto;
    }
    .btwpp{
      margin:20px auto;
    }
    #jobs{
      display: flex;
      flex-direction: column;
    }
    .card-design,.card-front-end, .card-ti{
      width: 80%;
      margin: 0 auto;
      padding-bottom: 5%;
      margin-bottom: 10%;
    }
    .card-design-icon, .card-front-end-icon, .card-ti-icon{
      width: 100% !important;
      background-size: 10% !important;
    }
    .card-design h1, .card-front-end h1, .card-ti h1{
      font-size: 2em;
    }
    .card-design p, .card-front-end p, .card-ti p{
      font-size: 1em;
      margin-bottom: 20px;
    }
    #portfolio .nome h1{
      letter-spacing: 2px;
    }
    #portfolio span:last-child{
        padding-bottom: 10px;
    }
    .oquefaco, .clientes, .estaodizendo, .fiqueligado{
      font-size: 2em;
    }
    #clientes{
      display: grid;
    }
    #box-cliente{
      width: 100%;
      margin:5px auto !important;
    }
    .img-cliente{
      width: 80%;
      height: 80%;
      text-align: center;
    }
    .t-cliente h1{
      font-size: 1em;
    }
    .t-cliente p{
      font-size: 0.8em;
    }
    #menu{
      padding:10px 0
    }
    #menu .menu{
      width: 90%;
    }
    #comentarios{
      width: 80%;
    }
    #comentario{
      width: 100%;
    }
    .desc-coment h1{
      font-size: 80%;
    }
  }
  
  @media screen and (max-width: 440px){
    #menu nav{
      display: none;
    }
    #portfolio{
      display: flex;
      width: 100% !important;
  
    }
    #top{
      height: 120vh !important;
      margin: 0 auto;
    }
    .oquefaco{
      margin-top: -40% !important;
    }
    #jobs{
      display: flex;
      flex-direction: column;
    }
    .portfolio{
      margin-top: -20% !important;
    }
    .avatar{
      width: 80%;
      height: 80%;
    }
  
    .nome h1{
      font-size: 2rem !important;
    }
    .nome  span{
      font-size: 1.3rem !important;
    }
    #menu{
      width: 100% !important;
      padding:10px 0;
    }
    .card-design,.card-front-end, .card-ti{
      width: 100%;
      margin: 0 auto;
      height: 100%;
      padding-bottom: 10%;
      margin-bottom: 15%;
    }
    .card-design-icon, .card-front-end-icon, .card-ti-icon{
      width: 100% !important;
      background-size: 10% !important;
    }
    .card-design h1, .card-front-end h1, .card-ti h1{
      font-size: 1.3em;
      margin: 10px 0;
    }
    .card-design p, .card-front-end p, .card-ti p{
      font-size: 1em;
      margin-bottom: 20px;
    }
    .oquefaco, .clientes, .estaodizendo, .fiqueligado{
      font-size: 1.5em;
      margin: 20px 0;
    }
    #clientes{
      display: grid;
      width: 85% !important;
    }
    #box-cliente{
      width: 100%;
      margin:15px auto !important;
    }
    .img-cliente{
      width: 80%;
      height: 80%;
      text-align: center;
    }
    .t-cliente h1{
      font-size: 1em;
    }
    .t-cliente p{
      font-size: 0.8em;
    }
    .iconLinkedin, .iconInsta,.iconCurriculum{
      width: 10vw;
    }
    .label{
      font-size: 0.6em;
    }
    #comentarios{
      display: grid;
      width: 80%;
      margin-bottom: 10vh;
      margin-top: 5vh;
    }
    #comentario{
      margin: 0 auto;
      width: 100%;
    }
    #menu .menu{
      width: 84%;
    }
  }
  